import { Typography, TextField, Button, Grid } from '@mui/material';
import MainCard from 'ui-component/cards/MainCard';
import { useState } from 'react';

const Page = () => {
  const [message, setMessage] = useState('');
  const [error, setError] = useState(false);
  const [outputData, setOutputData] = useState(null);
  const [outputError, setOutputError] = useState(null);
  const [hovered, setHovered] = useState(false);

  const handleClick = async () => {
    if (message.trim() === '') {
      setError(true);
    } else {
      setError(false);
      try {
        const data = await validateToken(message);

        setOutputData(data);
        setOutputError(null);
      } catch (err) {
        setOutputData(null);
        setOutputError(err.message);
      }
    }
  };

  const validateToken = async (token) => {
    const validateResponse = await fetch('https://id.twitch.tv/oauth2/validate', {
      headers: {
        Authorization: `OAuth ${token}`
      }
    });

    const validateData = await validateResponse.json();

    if (!validateResponse.ok) {
      switch (validateData.message) {
        case 'invalid access token':
          throw new Error('Invalid access token');
        default:
          throw new Error(validateData.message || 'Validate token failed');
      }
    }

    return validateData;
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      handleClick();
    }
  };

  const handleMouseEnter = () => {
    setHovered(true);
  };

  const handleMouseLeave = () => {
    setHovered(false);
  };

  return (
    <MainCard title="Validate Tokens">
      <Grid container spacing={2} alignItems="center">
        <Grid item xs={9}>
          <TextField
            fullWidth
            id="standard-basic"
            label="OAuth Token"
            type="password"
            variant="standard"
            value={message}
            onChange={(e) => setMessage(e.target.value)}
            error={error}
            helperText={error ? 'This field is required' : ''}
            onKeyPress={handleKeyPress}
          />
        </Grid>
        <Grid item xs={3}>
          <Button variant="contained" fullWidth onClick={handleClick}>
            Validate
          </Button>
        </Grid>
        <Grid item xs={12}>
          {outputData && (
            <div style={{ padding: '5px', marginTop: '10px' }}>
              <Typography variant="body2" component="pre">
                <code>
                  Login: <span style={{ color: '#00B8D9' }}>{outputData.login}</span> - (ID:{' '}
                  <span style={{ color: '#00B8D9' }}>{outputData.user_id}</span>)
                  <br />
                  Token:{' '}
                  <span
                    className={hovered ? '' : 'blur-on-hover'}
                    style={{ color: '#00B8D9' }}
                    onMouseEnter={handleMouseEnter}
                    onMouseLeave={handleMouseLeave}
                  >
                    {message}
                  </span>
                  <br />
                  ClientID: <span style={{ color: '#00B8D9' }}>{outputData.client_id}</span>
                  <br />
                  Scopes: <span style={{ color: '#00B8D9' }}>{outputData.scopes.join(', ')}</span>
                </code>
              </Typography>
            </div>
          )}
          {outputError && (
            <Typography variant="body2" style={{ color: 'red', marginTop: '10px' }}>
              {outputError}
            </Typography>
          )}
        </Grid>
      </Grid>
    </MainCard>
  );
};

export default Page;
